@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,400italic,600,600italic,700,700italic);

@import 'partials/normalize';
@import 'partials/vars';
@import 'partials/mixins';
@import 'partials/grid';

body {
	font-family: 'Open Sans', sans-serif;
	font-size: 15px;
}

h1, h2, h3, h4, h5, h6 {
	color: $main-color-orange + 30;
}

.wrapper, .row {
	@include clearfix();
}

.container {
	max-width: 960px;
	width: 100%;
	margin: 0 auto;
}

header {
	width: 100%;
	background-color: rgba(225, 225, 46, 0.5);
	text-align: center;
	
	.brand {
		img {
			width: 100%;
			max-height: 175px;
			background-size: contain;
		}
	}
}

.feat_wrapper {
	width: 70%;
	margin: 0 auto;
	height: 100%;

	.feature {
		height: 100%;
	}
}

.inner {

	.title {
		text-align: center;	
	}

	.content {
		width: 80%;
		margin: 0 auto;
		line-height: 1.4;
		color: $main-color-dark-gray - 30;

		ul {
			margin: 10px;
		}
	}

	.image {
		width: 100%;
		height: 135px;
		line-height: 135px;
		position: relative;
		text-align: center;
		cursor: pointer;

		.overlay {
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			opacity: 0;
			background-color: rgba(0,0,0,0.5);
			transition: opacity 0.3s ease;
		}

		&.img-1 {
			@include patterns("1");
		}

		&.img-2 {
			@include patterns("2");
		}

		&.img-3 {
			@include patterns("3");
		}

		&:hover {
			.overlay {
				opacity: 1;
			}

			.number {
				opacity: 0;
			}
		}

		h1 {
			color: $main-color-light-gray + 30;
			font-size: 18px;
			padding: 0;
			margin: 0;
		}

		.number {
			text-align: center;
			width: 40px;
			height: 40px;
			background-color: $main-color-dark-gray + 20;
			color: $main-color-light-gray + 30;
			line-height: 40px;
			font-size: 16px;
			font-weight: bold;
			border-radius: 100%;

			margin: 0 auto;
			position: relative;
			top: calc(50% - 20px);
			opacity: 1;
			transition: opacity 0.3s ease;
		}
	}
}