body {
  font-family: 'Open Sans', sans-serif;
  font-size: 15px; }

h1, h2, h3, h4, h5, h6 {
  color: #ff631e; }

.wrapper::before, .row::before {
  display: table;
  content: ''; }

.wrapper::after, .row::after {
  display: table;
  clear: both;
  content: ''; }

.container {
  max-width: 960px;
  width: 100%;
  margin: 0 auto; }

header {
  width: 100%;
  background-color: rgba(225, 225, 46, 0.5);
  text-align: center; }
  header .brand img {
    width: 100%;
    max-height: 175px;
    background-size: contain; }

.feat_wrapper {
  width: 70%;
  margin: 0 auto;
  height: 100%; }
  .feat_wrapper .feature {
    height: 100%; }

.inner .title {
  text-align: center; }

.inner .content {
  width: 80%;
  margin: 0 auto;
  line-height: 1.4;
  color: #3d3d3d; }
  .inner .content ul {
    margin: 10px; }

.inner .image {
  width: 100%;
  height: 135px;
  line-height: 135px;
  position: relative;
  text-align: center;
  cursor: pointer; }
  .inner .image .overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    background-color: rgba(0, 0, 0, 0.5);
    transition: opacity 0.3s ease; }
  .inner .image.img-1 {
    background-size: contain;
    background-repeat: repeat;
    background-image: url("https://patterns.forcir.com/v1/1"); }
  .inner .image.img-2 {
    background-size: contain;
    background-repeat: repeat;
    background-image: url("https://patterns.forcir.com/v1/2"); }
  .inner .image.img-3 {
    background-size: contain;
    background-repeat: repeat;
    background-image: url("https://patterns.forcir.com/v1/3"); }
  .inner .image:hover .overlay {
    opacity: 1; }
  .inner .image:hover .number {
    opacity: 0; }
  .inner .image h1 {
    color: #c8c8c8;
    font-size: 18px;
    padding: 0;
    margin: 0; }
  .inner .image .number {
    text-align: center;
    width: 40px;
    height: 40px;
    background-color: #6f6f6f;
    color: #c8c8c8;
    line-height: 40px;
    font-size: 16px;
    font-weight: bold;
    border-radius: 100%;
    margin: 0 auto;
    position: relative;
    top: calc(50% - 20px);
    opacity: 1;
    transition: opacity 0.3s ease; }
